.stopwatch-container{
  width: 99vw;
  height: 98vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  color:  white;
  font-size: 30px;
  h1{
    color: black;
    font-size: 40px;
  }
  .stapwatch{
    width: 15vw;
    height: 30vh;
    background-color: black;
    border-radius: 20%;
  }
  .stapwatch-output{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    text-align: center;
    .time{
      width: 50px;
    }
  }
  .button-block{
    width: 14vw;
    height: 10vh;
    display: flex;
    position: relative;
    margin-left: 10px;
    .btn{
      background-color: white;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      color: black;
      font-size: 25px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      user-select: none;
    }
    .start{
      cursor: pointer;
      top: 0;
      left: 0;
      position: absolute;
      z-index: 1;
    }
    .stop{
      top: 0;
      left: 0;
      cursor: pointer;
      position: absolute;
    }
    .clear{
      top: 0;
      right: 0;
      position: absolute;
      cursor: pointer;
    }
  }
  
}
.active{
  visibility: hidden;
  opacity: 0;
}